import React from 'react';
import { Link } from 'react-router-dom';
import { classNames } from 'shared/lib/classNames/classNames';
import cls from './Navbar.module.scss'
import { AppLinks, AppLinksTheme } from 'shared/ui/AppLink/AppLinks';
import { ThemeSwitcher } from 'widgets/ThemeSwitcher';

interface NavbarProps{
    className?: string
}

export const Navbar = ({className}:NavbarProps) => {
    return (
        <div className= {classNames(cls.navbar)}>
            <div className= {classNames(cls.links)}>
                <AppLinks theme= {AppLinksTheme.SECONDARY} to={'/'} className={classNames(cls.mainLink)}>Главная</AppLinks>
                <AppLinks theme= {AppLinksTheme.SECONDARY} to={'/about'}>Вторая</AppLinks>
            </div>
        </div>
    );
};
